import "swiper/css";
import "./styles/index.scss";
import Swiper, { Navigation, Pagination, Autoplay } from "swiper";

const swiper = new Swiper(".swiper", {
  // configure Swiper to use modules
  modules: [Navigation, Pagination, Autoplay],

  direction: "horizontal",
  loop: true,

  autoplay: {
    delay: 2000,
    disableOnInteraction: false,
  },

  breakpoints: {
    441: {
      slidesPerView: 2,
    },
    440: {
      slidesPerView: 1.35,
    },
    393: {
      slidesPerView: 1.2,
    },
    380: {
      slidesPerView: 1.15,
    },
    360: {
      slidesPerView: 1.1,
    },
  },
});

//Работа faq секции

const questions = document.querySelectorAll(".faq-question");

questions.forEach((q) =>
  q.querySelector(".faq-question-header").addEventListener("click", function () {
    q.querySelector(".faq-question-info").classList.toggle("active");
    q.querySelector(".question-arrow").classList.toggle("active")
  })
);
